:root {
    --color-primary: #2c3e50; /* 어두운 네이비 색상 */
    --color-secondary: #ecf0f1; /* 밝은 회색 */
    --color-accent: #3498db; /* 파란색 */
}

.admin-container {
    display: flex;
    height: 100vh;
    flex-direction: column;
}

.admin-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    background-color: var(--color-primary);
    color: var(--color-secondary);
}

.admin-title {
    font-size: 1.8rem;
    font-weight: bold;
    margin: 0;
}

.admin-main-container {
    display: flex;
    height: calc(100vh - 60px); /* 헤더 높이를 제외한 전체 높이 */
}

.admin-side {
    background-color: var(--color-primary);
    color: var(--color-secondary);
    width: 250px;
    padding: 1rem;
}

.admin-menu-list {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.admin-menu {
    font-size: 1.2rem;
    margin: 10px 0;
    cursor: pointer;
    position: relative;
}

.admin-menu-dropdown {
    display: none;
    position: absolute;
    left: 100%;
    top: 0;
    background-color: var(--color-primary);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    z-index: 1;
    width: 200px;
}

.admin-menu-dropdown li {
    padding: 10px;
    text-align: left;
}

.admin-menu:hover .admin-menu-dropdown {
    display: block;
}

.admin-main {
    padding: 2rem;
    width: 100%;
    overflow-y: auto;
}

.admin-main-title {
    width: 100%;
    font-size: 2rem;
    text-align: center;
    padding: 1rem;
    border-bottom: 1px solid var(--color-primary);
    margin-bottom: 2rem;
}

.admin-search {
    width: 80%;
    margin: 0 auto;
    padding: 1rem 0;
    gap: 1rem;
    display: flex;
}

.admin-search select, .admin-search input, .admin-search button {
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}

.admin-search button {
    background-color: var(--color-accent);
    border-radius: 5px;
    color: var(--color-secondary);
}

.admin-user-list {
    margin: 0 auto;
    width: 80%;
    border-collapse: separate;
    border-spacing: 0 10px;
}

.admin-header {
    width: 100%;
    font-size: 1.2rem;
}

.admin-header th {
    width: 200px;
    text-align: left;
    background-color: #f2f2f2;
}

.admin-header th, .admin-data td {
    padding: 8px;
    border: 1px solid #dddddd;
}

.admin-user-list .admin-data td {
    background-color: #ffffff;
}
